//  
@import "./bootstrap/css/bootstrap.css";
@import "./fonts/font-awesome/css/font-awesome.css";
@import "./css/style.css";
@import "./css/skins/light_blue.css";
@import "./css/animations.css";

* {
  /* padding  box     .     element . */
  box-sizing: border-box;
}

.section {
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  object-position: center;
  background-position: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.section > .container {
  margin-top: -150px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.full-image {
  height: 100%;
  width: 100%;
}

.image-box .img-sm-center {
  max-height: 180px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-box .img-md-center {
  max-height: 294.2px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.timeline-title {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0 30px 10px;
}

.text-nanum {
  font-family: "Nanum Gothic", sans-serif;
}

.text-nanum-md {
  font-family: "Nanum Gothic", sans-serif;
  font-weight: 400;
}

.text-nanum-lg {
  font-family: "Nanum Gothic";
  font-weight: 700;
}

.text-muli {
  font-family: "Muli", sans-serif;
}

.text-noto {
  font-family: "Noto Sans KR", sans-serif;
}

.text-dance {
  font-family: "Dancing Script", cursive;
}

.text-sm {
  font-weight: 200;
}

.text-md {
  font-weight: 300;
}

.text-normal {
  font-weight: 400;
}

.text-bold {
  font-weight: 700;
}

.font-sm {
  font-size: 12pt;
}

.text-lightgreen {
  color: #00cc00;
}

.text-blue2 {
  color: #0066ff;
}

.text-border {
  text-shadow: 0 0 2px rgba(87, 101, 116, 1);
}

.text-border-lg {
  text-shadow: 2px 2px 4px rgba(87, 101, 116, 1);
}

.text-border-none {
  text-shadow: none;
}

.elem-dropdown {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
  animation: dropdown forwards 0.3s ease-in-out;
}

@keyframes dropdown {
  0% {
    transform: translateY(-100px);
  }
  100% {
    transform: translateY(px);
  }
}

#next-page {
  padding-top: 10px;
}

#next-page span {
  position: absolute;
  width: 24px;
  height: 24px;
  margin-left: -12px;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -webkit-animation: sdb 1.5s infinite;
  animation: sdb 1.5s infinite;
  box-sizing: border-box;
}

#next-page span:nth-of-type(1) {
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
}

#next-page span:nth-of-type(2) {
  margin-top: 16px;
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s;
}

#next-page span:nth-of-type(3) {
  margin-top: 32px;
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}

@-webkit-keyframes sdb {
  0% {
    -webkit-transform: rotate(-45deg) translate(0, 0);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: rotate(-45deg) translate(-20px, 20px);
    opacity: 0;
  }
}

@keyframes sdb {
  0% {
    transform: rotate(-45deg) translate(0, 0);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: rotate(-45deg) translate(-20px, 20px);
    opacity: 0;
  }
}
